import loading from '@/components/loading/loading.vue'
export default {
	components: {
		loading
	},
	data() {
		return {
			//关键词
			value: '',
			hotTags: [],
			isLoadHotTags: false,
			isLoadHotTagsEnd: false,
			historyTags: [],
			isShowList: false,
			paddingTop: 0,

			list: [],
			isLoadList: false,
			isLoadEnd: false,
			limit: 20,
			offset: 0,
			//是否用币种功能
			isCoin: false,
			historyWord: '',
		}
	},
	methods: {
		//因为从小程序搬到uni-app，所以需要兼容setData方法
		setData: function(obj) {
			let that = this;
			let keys = [];
			let val, data;
			Object.keys(obj).forEach(function(key) {
				keys = key.split('.');
				val = obj[key];
				data = that.$data;
				keys.forEach(function(key2, index) {
					if (index + 1 == keys.length) {
						that.$set(data, key2, val);
					} else {
						if (!data[key2]) {
							that.$set(data, key2, {});
						}
					}
					data = data[key2];
				})
			});
		},

		onKeyInput: function(event) {
			this.value = event.target.value
		},

		cancel: function() {
			uni.navigateBack()
		},

		cancelLight: function() {
			this.setData({
				isShowList: false,
				value: ''
			})
		},

		/**
		 * 从storage加载历史关键词
		 */
		getHistorySearchWord: function() {
			var arrayListJson = uni.getStorageSync(this.historyWord);
			var result = []
			if (arrayListJson) {
				result = JSON.parse(arrayListJson)
			}
			return result
		},

		saveHistorySearchWord: function(word) {
			var list = this.getHistorySearchWord()
			var sameIndex = -1
			for (var i = 0; i < list.length; i++) {
				if (list[i] == word) {
					sameIndex = i
					break
				}
			}
			if (sameIndex != -1) {
				list.splice(sameIndex, 1)
			}
			list.unshift(word)
			this.setData({
				historyTags: list
			})
			uni.setStorageSync(this.historyWord, JSON.stringify(list))
		},

		deleteHistoryWord: function(e) {
			// console.log(e)
			var targetWord = e.currentTarget.dataset.word
			var list = this.getHistorySearchWord()
			var sameIndex = -1
			for (var i = 0; i < list.length; i++) {
				if (list[i] == targetWord) {
					sameIndex = i
					break
				}
			}
			if (sameIndex != -1) {
				list.splice(sameIndex, 1)
			}
			this.setData({
				historyTags: list
			})
			uni.setStorageSync(this.historyWord, JSON.stringify(list))
		},

		clearHistoryWord: function() {
			var list = []
			this.setData({
				historyTags: list
			})
			uni.setStorageSync(this.historyWord, JSON.stringify(list))
		},

		selectSearchWord: function(e) {
			var targetWord = e.currentTarget.dataset.word
			this.setData({
				value: targetWord
			})
			this.saveHistorySearchWord(targetWord)
			this.setData({
				isShowList: true
			})
			this.loadList(true)
		},

		confirm: function(event) {
			var value = event.detail.value
			this.setData({
				value: value
			})
			if (value) {
				this.saveHistorySearchWord(value)
			}
			this.setData({
				isShowList: true
			})
			this.loadList(true)
		},

		loadHot: function() {
			uni.showNavigationBarLoading()
			this.setData({
				isLoadHotTags: true
			})
			var self = this
			uni.request({
				url: getApp().globalData.domain + '/api/common/hot-tags',
				dataType: 'json',
				success: function(res) {
					console.log(res.data)
					uni.hideNavigationBarLoading()
					self.setData({
						hotTags: res.data,
						isLoadHotTags: false,
						isLoadHotTagsEnd: true
					})
				},
				fail: function() {
					this.setData({
						isLoadHotTags: false,
						isLoadHotTagsEnd: true
					})
					uni.hideNavigationBarLoading()
					uni.showToast({
						title: '网络错误，请重新加载',
						icon: 'none',
						duration: 2000
					})
				}
			})
		},

		/**
		 * isReset 是否重置查询
		 */
		loadList: function(isReset) {
			if (this.isLoadList == true) {
				return
			}
			if (isReset) {
				this.setData({
					offset: 0,
					isLoadEnd: false,
					isLoadList: true
				})
			} else {
				if (this.isLoadEnd) {
					return
				}
				this.setData({
					isLoadList: true
				})
			}
			uni.showNavigationBarLoading()
			var self = this
			var paramData = {
				q: this.value,
				limit: this.limit,
				offset: this.offset,
				t: this.isCoin ? 'token' : 'article'
			}
			uni.request({
				url: getApp().globalData.domain + '/api/search',
				data: paramData,
				dataType: 'json',
				success: function(res) {
					var list = res.data.rows
					// var list = []
					// for (var i = 0; i < res.data.rows.length; i++) {
					//   list.push({
					//     title: res.data.rows[i].title,
					//     tags: res.data.rows[i].tags,
					//     author: res.data.rows[i].author,
					//     updateTime: res.data.rows[i].updateTime,
					//     createTime: res.data.rows[i].createTime,
					//     reviewCount: res.data.rows[i].reviewCount,
					//     likeCount: res.data.rows[i].likeCount,
					//     preview: res.data.rows[i].preview,
					//     'type': res.data.rows[i].type,
					//     id: res.data.rows[i].id,
					//     imageArray: res.data.rows[i].imageArray
					//   })
					// }
					var obj = {
						// list: isReset ? res.data.rows : getApp().globalData.merge(self.list, res.data.rows),
						list: isReset ? list : getApp().globalData.merge(self.list, list),
						isLoadList: false,
						isLoadEnd: self.isLoadEnd,
						offset: self.offset
					}
					//判断是否可以继续下一页
					if (res.data.rows.length < self.limit) {
						obj.isLoadEnd = true
					} else {
						obj.isLoadEnd = false
						obj.offset = self.offset + self.limit
					}
					self.setData(obj)
					uni.hideNavigationBarLoading()
				},
				fail: function() {
					self.setData({
						isLoadList: false
					})
					uni.hideNavigationBarLoading()
					uni.showToast({
						title: '网络错误，请重新加载',
						icon: 'none',
						duration: 2000
					})
				}
			})
		},

		goToDetail: function(params) {
			// console.log(params)
			var index = params.target.dataset.index
			if (this.isCoin) {
				uni.navigateTo({
					url: "../chain_detail/chain_detail?id=" + this.list[index].id + "&title=" + this.list[index][
						'symbol'
					]
				})
			} else {
				uni.navigateTo({
					url: "../detail/detail?id=" + this.list[index].id + "&title=" + this.list[index].title,
				})
			}
		},
	},



	/**
	 * 生命周期函数--监听页面加载
	 */
	onLoad: function(options) {
		console.log(options)
		options.isCoin = '1'
		this.setData({
			isCoin: options.isCoin == '1',
			historyWord: options.isCoin == '1' ? getApp().globalData.storageKey.historyWordB : getApp().globalData.storageKey
				.historyWord
		})
		this.loadHot()
		this.setData({
			historyTags: this.getHistorySearchWord()
		})
		var self = this
		uni.getSystemInfo({
			success: function(res) {
				self.setData({
					paddingTop: res.windowHeight / 2 - 100
				})
			},
		})
	},

	/**
	 * 生命周期函数--监听页面初次渲染完成
	 */
	onReady: function() {

	},

	/**
	 * 生命周期函数--监听页面显示
	 */
	onShow: function() {

	},

	/**
	 * 生命周期函数--监听页面隐藏
	 */
	onHide: function() {

	},

	/**
	 * 生命周期函数--监听页面卸载
	 */
	onUnload: function() {

	},

	/**
	 * 页面相关事件处理函数--监听用户下拉动作
	 */
	onPullDownRefresh: function() {

	},

	/**
	 * 页面上拉触底事件的处理函数
	 */
	onReachBottom: function() {
		this.loadList(false)
	},

}
